.main{
    padding: 10px;
    min-height:100%;
    /* overflow-y: scroll; */
}
.layout{
    display: flex;
}
.imagelogo{
    /* background-color: red; */
    height: 250px;
    width: 245px;
    margin-top: 200px;
    background-image: url(../assets/doctor.png);
    background-size: cover;
    position: absolute;
    bottom: 50px;
    z-index: 999;
    bottom: -10px;
}
.sidebar{
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: rgb(95, 111,255);
    /* background: linear-gradient(to bottom, rgb(95, 111, 255),white); */
    box-shadow: 0 0 2px grey;
    margin-right: 20px;
    color: white;
}

.content{
    width: 100%;
    height: 100%;
}

.header{
    /* background: linear-gradient(to right, rgb(95, 111, 255),white); */
    /* background-color: rgba(95, 111, 255, 0.646); */
    display: flex;
    align-items: center;
    justify-content: end;
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: white;
}

.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: white;
}
.logo h6{
    font-size: 1.5rem;
    margin: 20px 0px;
    text-align: center;
}
.menu{
    margin-top: 30px;
}
.menu-item{
    margin-top:30px;
}
.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}
.menu-item i{
    font-size: 1.2rem;
    margin:0 15px ;
}
.active{
    background-color: white;
    padding: 10px;
    font-weight: bold;
}
.active a, .active i{
    color:  rgb(56, 34, 34);
}

.header-content{
    /* background-color: rgb(95, 111,255); */
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 50px;
    margin: 0 15px;
}
.header-content i{
/* margin-right: 20px; */
font-size: 1.2rem;
}
.header-content a{
    text-decoration: none;
    font-weight: bold;
    color: black;
    font-size: 1.2rem;
    margin: 0 10px;
    text-transform: uppercase;

}
.logo-d{
    display: none;
}
@media (max-width:680px) {
    .sidebar{
        display: none;
    }
    .logo-d{
        gap: 10px;
        display: flex;
        /* align-items: center; */
        margin-top: 9px;
        font-weight: bold;
    }
    .header{
        justify-content: space-between;
        padding: 5px;
        background-color: rgb(95, 111,255);
    }
    
}